.chart-container {
    width: 100%;
    max-width: 1200px;
    height: 700px;
    margin-bottom: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    overflow: hidden;
    position: relative;
}

.bar-chart-container {
    width: 1200px;
    height: 600px;
}

.pie-chart-container {
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.chart-container .legend {
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-align: center;
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 8px;
    padding: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Стили для заголовка графика */
.chart-container .chart-title {
    text-align: center;
    font-size: 24px;
    margin-bottom: 16px;
    color: #333;
}

.chart-container .legend-item {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    color: #555;
}

.chart-container .legend-item::before {
    content: '';
    display: inline-block;
    width: 16px;
    height: 16px;
    margin-right: 8px;
    border-radius: 50%;
}

.chart-container .legend-item:nth-child(1)::before {
    background-color: #1c00ff;
}

.chart-container .legend-item:nth-child(2)::before {
    background-color: #000000;
}
